html{

  padding: 0;
  margin: 0;
  box-sizing: border-box;
  background-color: #f3f3f3;

}
body{
  margin: 0;
 overflow-y: hidden;
}

#root{
  height:100%;
  width: 100%;
}
.App{
  
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: 'Georama', sans-serif;
  color: black;

}


.header{
margin: 0;
padding: 3px;
font-size: 2.3rem;
font-weight: bold;
font-style: italic;
margin-bottom: 20px;
background-color: #020020;




}
.headerbackground{
background-image:  linear-gradient(to left,#F95F3D,#FCBA4C);
background-clip: text;
-webkit-text-fill-color: transparent;

}

